.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card img{
  width: 100%;
  height: 237px;
  object-fit: cover;
}
.badge{
  margin-bottom: 13px;
}
.heading{
  margin-top: 72px;
}

.nav-item a:hover{
   
  color: white;

}
.footer{
  background: black;
  color: white;
  width: 100%;
  height: 60px;
  line-height: 55px;
}